




























import { Component, Vue } from "vue-property-decorator";
import { adminModule } from "@/store/modules";
import ClientEditor from "@/components/misc/ClientEditor.vue";
import ClientList from "@/components/clients/ClientsList.vue";
import SpeedDial, { ISpeedDialItem } from "@/components/misc/SpeedDial.vue";

@Component({
  components: {
    ClientEditor,
    ClientList,
    SpeedDial
  }
})
export default class Admin extends Vue {
  async mounted() {
    await adminModule.fetchAllClients();
  }
  get clients() {
    return adminModule.allClients;
  }
  get clientsCount() {
    return adminModule.allClients.length;
  }

  get speedDialItems(): ISpeedDialItem[] {
    return [
      {
        to: "/admin/client/new",
        icon: "mdi-plus",
        name: this.$t("admin.craete-new-client").toString()
      }
    ];
  }
}
